/* Meta tag for color scheme */
.light-mode {
  --background-color: #ffffff;
  --text-color: #000;
  --shadow-color: #000;
  --border-color: #000;
}

.dark-mode {
  --background-color: #1c1c1c;
  --theme-green-color: #b6ff1b;
  --text-color: #f6f6f6;
  --text-color-secondary: #858585;
  --text-color-button: #ffffff;
  --text-color-button-secondary: #dfdfdf;
  --text-color-button-hover: #000000;
  --text-color-button-secondary-hover: #5c5c5c;
  --shadow-color: #292e32;
  --border-color: #242424;
  --button-color: #868686;
  --button-hover-color: #b6ff1b;
  --container-higlight-color: #b6ff1b;
  --question-text-higlight-color: #b7ff1b6b;
  --result-link-color: #b6ff1b;
  --result-link-hover-color: #1d2b00;
}

/*------------------------------*/
/* Base Styles (default for desktop) */
body {
  font-family: 'Open Sans', sans-serif;
  background-color: #161616;
  color: var(--text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(var(--vh, 1vh) * 100);
  margin: 0;
}

header {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.box {
  background-color: var(--background-color);
  padding: 35px 25px;
  text-align: center;
  color: var(--text-color);
  min-width: 300px;
  max-width: 600px;
  border: 1px solid var(--border-color);
  border-radius: 5px;
}

img {
  max-width: 100%;
  height: auto;
}

p {
  color: var(--text-color-secondary);
}

p.result a {
  color: var(--result-link-color);
  font-size: 30px;
  font-family: 'Roboto Mono', sans-serif;
  text-decoration: none;
  text-decoration-line: none !important;
  border-radius: 5px;
  transition: ease-in-out 200ms;
  padding: 10px;
}

p.result a:hover {
  background-color: var(--result-link-hover-color);
  border-radius: 5px;
}

.question-box img {
  max-width: 100%;
  height: auto;
  margin-bottom: 20px;
  border-radius: 10px;
}

.question-header {
  /* display: flex; */
  justify-content: space-between;
  /* position: absolute; */
  top: 0;
  right: 0;
}

button {
  padding: 10px 20px;
  cursor: pointer;
  background-color: var(--button-color);
  color: var(--text-color-button);
  border: none;
  transition: background-color 0.3s;
  border-radius: 5px;
  font-weight: bold;
  font-size: 16px;
  width: 100%;
  margin-top: 10px;
}

button p {
  color: var(--text-color-button-secondary);
  font-weight: normal !important;
}

button.controlButton {
  width: 40px;
  height: 40px;
  padding: 2px;
  margin-right: 5px;
  border-radius: 50%;
  font-size: 20px;
}

button.decisionButton {
  border-radius: 5px;
  width: 45%;
}

#decisionBox {
  justify-content: space-between;
  display: flex;
  margin-top: 20px;
}

button:hover {
  background-color: var(--button-hover-color);
  color: var(--text-color-button-hover);
}

button:hover p {
  color: var(--text-color-button-secondary-hover);
}

button:last-child {
  margin-right: 0;
}

.box h3 {
  color: var(--theme-green-color) !important;
  font-size: 18px;
  text-shadow: var(--question-text-higlight-color) 1px 1px 10px;
}

/* Mobile Styles (up to 600px) */
@media (max-width: 600px) {
  body {
    height: calc(var(--vh, 1vh) * 100);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }

  .box {
    padding: 20px;
    width: 100%;
    max-width: 300px;
    margin: 0 10px;
  }

  header {
    flex-direction: column;
    align-items: flex-start;
  }

  button {
    padding: 10px;
    font-size: 14px;
  }

  button.decisionButton {
    width: 100%;
    margin: 10px 0;
  }

  #decisionBox {
    flex-direction: column;
  }
}

/* Tablet Styles (601px to 1024px) */
@media (min-width: 601px) and (max-width: 1024px) {
  body {
    height: auto;
    padding: 20px;
  }

  .box {
    padding: 25px;
    width: 80%;
    max-width: 600px;
    margin: 0 auto;
  }

  header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  button {
    padding: 10px;
    font-size: 15px;
  }

  button.decisionButton {
    width: 48%;
  }

  #decisionBox {
    flex-direction: row;
    flex-wrap: wrap;
  }
}


/****************/
.loader {
  /*position: absolute;*/
  top: calc(50% - 32px);
  left: calc(50% - 32px);
  width: 100px;
  height: 100px;
  border-radius: 50%;
  perspective: 800px;
  margin: 0 auto;
}

.inner {
  position: absolute;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}

.inner.one {
  left: 0%;
  top: 0%;
  animation: rotate-one 1s linear infinite;
  border-bottom: 3px solid var(--theme-green-color);
}

.inner.two {
  right: 0%;
  top: 0%;
  animation: rotate-two 1s linear infinite;
  border-right: 3px solid var(--theme-green-color);
}

.inner.three {
  right: 0%;
  bottom: 0%;
  animation: rotate-three 1s linear infinite;
  border-top: 3px solid var(--theme-green-color);
}

@keyframes rotate-one {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes rotate-two {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes rotate-three {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}

/* Enhanced Fade transition */
.fade-enter {
  opacity: 0;
  transform: scale(0.95);
}

.fade-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}

.fade-exit {
  opacity: 1;
  transform: scale(1);
}

.fade-exit-active {
  opacity: 0;
  transform: scale(0.95);
  transition: opacity 500ms ease-in-out, transform 500ms ease-in-out;
}